import React, {useLayoutEffect} from "react";
import {Canvas} from "@react-three/fiber";
import {OrbitControls} from "@react-three/drei";
import anime from "animejs";
import {useTranslation} from "react-i18next";
import Model from "../../3DModels/Goe"

export default function HeroBlock() {
    const {t} = useTranslation();

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#we-are-content',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, []);

    return (
        <div id="we-are" className="md:container mx-auto md:min-h-screen flex justify-center align-middle relative">
            <div className="grid grid-cols-1 lg:grid-cols-12 pt-32 md:pt-0">
                <div id="we-are-content" className="lg:col-span-6 flex justify-center items-center flex-col py-12 lg:py-0">
                    <div className="flex flex-col justify-center relative text-center sm:text-left w-full sm:w-3/4 lg:w-auto">
                        <h1 className="css--we-are-header text-center" style={{zIndex: 2}}>{t('about_us')}</h1>
                    </div>
                    <div className="mt-10 px-4 xl:px-0 text-center text-white md:text-lg">{t('about_description')}</div>
                </div>
                <div className="col-span-6 h-screen hidden lg:block css--we-are-hero">
                    <div style={{width: "100%", height: "100%", position: "relative"}}>
                        <Canvas
                            gl={{alpha: true}}
                            camera={{position: [0, 0, 8], fov: 40}}>
                            <OrbitControls makeDefault autoRotate enablePan={false} enableZoom={false} autoRotateSpeed={0.5} zoomSpeed={0.1}/>
                            <directionalLight intensity={1} position={[2, 2, 0]} color="#2fc7e5" distance={5} />
                            <React.Suspense fallback={null}>
                                <Model/>
                            </React.Suspense>
                        </Canvas>
                    </div>
                </div>
            </div>
            <div className="css--weare-lines-parent md:grid hidden" style={{ left: '2%'}}>
                <div className="css--weare-line-left">
                    <div className="css--weare-div-left">
                        <div className="css--weare-text-div bg-white text-black">{t('what_we_do')}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}
