import '../i18n/i18n';

import React from "react"
import Navigation from "../components/layout/Navigation";
import WeAreBlock from "../components/layout/WeAre";
import {SEO} from "../components/seo";

export default function AboutUs() {
    return (
        <main className="main-wrapper relative">
            <Navigation pageNr={'02'}/>
            <section className="pb-16">
                <WeAreBlock/>
            </section>
        </main>
    )
}

export const Head = () => <SEO title={"About Us"} description={'We are a software development company and we have built multiple digital products for our customers and for ourselves. We have experience in developing tailored solutions to meet client requirements in a short amount of time. Moreover, we would like to point out that we have done it by using various technologies and approaches which shows our flexibility and skill set.'}/>