import React, {useLayoutEffect} from "react";
import anime from "animejs";
import HeroBlock from "./Hero";
import WeAreDoing from "./WeAreDoing";
import FadedText from "../../elements/FadedText";
import Partners from "./Partners";

const WeAreBlock = () => {

    useLayoutEffect(() => {
        require('../../../scss/we_are_block.scss');

        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#we-are-content',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 800,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, []);

    return (
        <>
            <FadedText text={'DISCOVER'} direction="bottom"/>

            <HeroBlock/>
            <WeAreDoing/>
            <Partners />
        </>
    )
}
export default WeAreBlock;
