import React from "react";
import {graphql, Link, useStaticQuery} from "gatsby"
import Img from "gatsby-image";
import {useTranslation} from 'react-i18next';

export default function Partners() {
    const {t, i18n} = useTranslation();
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
                filter: {categories: {nodes: {elemMatch: {name: {eq: "partners"}}}}}
      ) {
          nodes {
            id
            title
            slug
            excerpt
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fixed(height: 70, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `);

    return (
        <div className="md:container mx-auto md:min-h-screen md:px-28" style={{zIndex: '10'}}>
            <h1 className="css--we-are-header text-center mb-8" data-glitch={t('our_partners')}>
                {t('our_partners')}
            </h1>
            <div className="grid grid-cols-1 mt-6">
                {data.allWpPost.nodes.map((node, index) => (
                    node.language.slug === i18n.language &&

                    <div key={index} className="mt-24">
                        <div className="grid grid-rows-1 lg:grid-cols-12  grid-flow-col gap-4">
                            <div className="row-span-3 flex justify-center">
                                <div className="css--we-are-decor-inner relative" style={{width: '34px', height: '100%'}}>
                                    <img className="object-cover h-full" src={"/images/discover_expand/rectangle_inner.svg"} alt="rectangle_inner"/>
                                </div>
                            </div>
                            <div className="col-span-2 lg:col-span-11">
                                <div className="css--solutions-logo pr-10">
                                    <Img objectFit="contain" className="h-full w-full" fixed={node.featuredImage.node.localFile.childImageSharp.fixed} alt="Partner image"/>
                                </div>
                            </div>
                            <div className="row-span-1 col-span-2 lg:col-span-11 text-white">
                                <div className="" dangerouslySetInnerHTML={{__html: node.excerpt}}/>
                                <div className="pt-6">
                                    <Link className="text-decoration-none css--solutions-href" to={'/partners/' + node.slug + "/"}>{t('read_more')}</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}

            </div>
        </div>
    )
}


