import React from "react";
import {useTranslation} from "react-i18next";

export default function WeAreDoing() {
    const {t} = useTranslation();

    return (
        <div className="py-32 md:mb-24 md:pt-0 min-h-screen flex items-center relative">
            <div className="md:container mx-4 md:mx-auto" style={{zIndex: '10'}}>
                <div className="grid grid-cols-1 gap-4 flex justify-center">
                    <div className="flex justify-center">
                        <h2 className="css--we-are-header text-center mb-8">{t('field_of_expertise')}</h2>
                    </div>
                </div>

                <div className="grid grid-cols-1">
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">01</span><span className="text-white text-xl md:text-3xl">{t('e-commerce_dev')}</span></h4>
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">02</span><span className="text-white text-xl md:text-3xl">{t('saas_dev')}</span></h4>
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">03</span><span className="text-white text-xl md:text-3xl">{t('iot_and_custom')}</span></h4>
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">04</span><span className="text-white text-xl md:text-3xl">{t('mobile_apps')}</span></h4>
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">05</span><span className="text-white text-xl md:text-3xl">{t('websites_dev')}</span></h4>
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">06</span><span className="text-white text-xl md:text-3xl">{t('api_dev')}</span></h4>
                    <h4 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">07</span><span className="text-white text-xl md:text-3xl">{t('any_other')}</span></h4>
                </div>
            </div>
            <div className="css--weare-lines-parent md:grid hidden" style={{ right: '5%'}}>
                <div className="css--weare-line-right">
                    <div className="css--weare-div-right">
                        <div className="css--weare-text-div bg-white text-black">{t('partners')}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}


